body {
  margin-left: 100px;
  margin-right: 100px;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #282c34;
  color:white;
}

h1 {
  font-size:45px;
}
h2 {
  font-size:35px;
}

.image {
  max-height:300px;
  max-width:100%;
}

.shadowed {
  box-shadow:5px 5px 3px #20242E;
}

.left {
  margin-top:10px;
  text-align: left;
}

#logo {
  width:100px;
  height:100px;
}

table, th, td {
  border:1px solid white;
  text-align: center;
}

td{
  padding:2px;
}

a, a:hover {
  color:cyan
}

a:active {
  color: rgb(0, 204, 255)
}

Button {
  margin:2px;
  float:left;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
